body {
    margin: 0;
    padding: 0;
    background-color: #eff3f7;
    height: 100%;
}
html, body, #root {
    height: 100%;
    display: flex;
    justify-content: center;
}
* {
    box-sizing: border-box;
}
.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px; 
    padding-top: 70px;
    width: 80%;
}
